// 1) , 2) , 3)  

//  (types.js) import
import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
} from "../actions/types";

//  user    
const user = JSON.parse(localStorage.getItem("user"));

//  
// user  null  : isLoggedIn(false), user(null)
//      ->  
// user  null  : isLoggedIn(true), user()
//      ->  
const initialState = user
  ? { isLoggedIn: true, user }
  : { isLoggedIn: false, user: null };

//   
export default function (state = initialState, action) {
  //  
  // action.type(), action.payload()
  const { type, payload } = action;

  // type : 
  switch (type) {
    //   
    case REGISTER_SUCCESS:
      return {
        ...state,
        isLoggedIn: false,   //  = false
      };
      //   
    case REGISTER_FAIL:
      return {
        ...state,
        isLoggedIn: false,   //  = false
      };
      //  
    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedIn: true,    //  = true
        user: payload.user,  // user = 
      };
      //   
    case LOGIN_FAIL:
      return {
        ...state,
        isLoggedIn: false,   //  = false
        user: null,          // user = null
      };
      //   
    case LOGOUT:
      return {
        ...state,
        isLoggedIn: false,   //  = false
        user: null,          // user = null
      };
      // 
    default:
      return state;
  }
}
