import React, { useState, useEffect } from "react";
//  (CRUD)
import MypageDataService from "../services/MyPageDataService";
import UserDateService from "../services/UserDataService";
// useParams :  (cid)   import
import { useParams, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import Mypagelist from "./MyPage/Mypagelist";

import "../assets/css/mystyle.css";

function Editmember() {
  //  
  // () (mno)  
  const { uno } = useParams();

  // let navigate = useNavigate();
  // const editNavi =()=>{
  //   navigate("#");
  //  };

  const newform = () => {
    // Add    

    setCurrentEditmem(initialEditmemberState);
  };

  // *    
  const initialEditmemberState = {
    mno: null,
    id: "",
    password: "",
    username: "",
    email: "",
    phone: "",
  };

  //   ( )  
  const [currentEditmem, setCurrentEditmem] = useState(initialEditmemberState);
  //     
  const [message, setMessage] = useState("");

  //  
  //   (mno) 1()  
  const getEditmem = (uno) => {
    // id(cid)  ?(mno)  
    UserDateService.getUserId(uno)
      .then((response) => {
        // : response.data(  )
        //      
        setCurrentEditmem(response.data);
        //   
        console.log(response.data);
      })
      .catch((e) => {
        // 
        console.log(e);
      });
  };

  //     
  // [] :      
  useEffect(() => {
    // (mno)  (getEditmem(mno)) 
    if (uno) getEditmem(uno);
  }, [uno]);

  //   :   ->    
  const handleInputChange = (event) => {
    const { name, value } = event.target; // 
    //  name      input  name,value?
    //   
    setCurrentEditmem({ ...currentEditmem, [name]: value });
  };

  

  //    
  const updateEditmem = () => {
    //   (id, )
    MypageDataService.update(currentEditmem.uno, currentEditmem)
      .then((response) => {
        //  : response.data(  )
        //   
        console.log(response.data);
        //    
        setMessage(".");
      })
      .catch((e) => {
        // 
        console.log(e);
      });
  };

  const [currentEdit, setCurrentEdit] = useState("");

  useEffect(()=>{
   let localuser=  JSON.parse(localStorage.getItem("user"))  
   console.log(localuser.id)  
   setCurrentEdit(localuser.id)                                                           
},[]);

  return (
    
    <div className="myPapeContainer">
      <div className="myPageTop">
        <div className="myPageBTit">
          <p>My Page</p>
        </div>
        <div className="myPageTit">
          <p>  </p>
        </div>
      </div>
      <div className="myPageContents">
      <div className="myPageCom">
            <Mypagelist />
          </div>
      </div>

      <div>
        <div id="edit_container">
          <form action="#" id="editmemfrom">
            {/* <!-- fieldset :  ()  html  --> */}
            <fieldset>
              {/* <!-- legend :    html  --> */}
              <legend>   </legend>
              {/* <!--   --> */}
              <ul id="user-info">
                <li>
                  {/* <!-- label  input    --> */}
                  {/* <!-- input - required :  --> */}
                  <label htmlFor="username" class="edit_field">
                    
                  </label>
                  <input
                    type="text"
                    id="username"
                    class="edit_input-box"
                    placeholder=""
                    required
                    value={currentEditmem.username}
                    onChange={handleInputChange}
                    name="username"
                  />
                </li>
                {/* name  ..?   name    name */}

                <li>
                  {/* <!-- label  input    --> */}
                  {/* <!-- input - required :  --> */}
                  <label htmlFor="id" class="edit_field">
                    
                  </label>
                  <input
                    type="text"
                    id="id"
                    class="edit_input-box"
                    placeholder="  "
                    required
                    value={currentEditmem.id}
                    onChange={handleInputChange}
                    name="id"
                  />
                </li>

                <li>
                  {/* <!--  --> */}
                  {/* <!--   : ,  --> */}
                  <label htmlFor="password" class="edit_field">
                    
                  </label>
                  <input
                    type="password"
                    id="password"
                    class="edit_input-box"
                    placeholder="8   "
                    minlength="8"
                    required
                    value={currentEditmem.password}
                    onChange={handleInputChange}
                    name="password"
                  />
                </li>

                <li>
                  {/* <!--  --> */}
                  {/* <!--   : ,  --> */}
                  <label htmlFor="password" class="edit_field">
                     
                  </label>
                  <input
                    type="password"
                    id="password"
                    class="edit_input-box"
                    placeholder="8   "
                    minlength="8"
                    required
                    value={currentEditmem.password}
                    onChange={handleInputChange}
                    name="password"
                  />
                </li>

                <li>
                  {/* <!-- email --> */}
                  {/* <!-- input type = email ->  --> */}
                  <label htmlFor="email" class="edit_field">
                    
                  </label>
                  <input
                    type="email"
                    class="edit_input-box"
                    id="email"
                    required
                    value={currentEditmem.email}
                    onChange={handleInputChange}
                    name="email"
                  />
                </li>
              </ul>
              {/* <!--   --> */}
              <ul id="edit_buttons">
                {/* <!--   --> */}
                <li>
                  <button
                    type="submit"
                    class="btn btn-outline-dark"
                    onClick={updateEditmem}
                  >
                    
                  </button>
                  {/*     */}
                  <p>{message}</p>
                </li>
                {/* <!--   --> */}
                <li>
                  <button
                    type="reset"
                    class="btn btn-outline-dark"
                    onClick={newform}
                  >
                    
                  </button>
                </li>
              </ul>
              {/* <!-- /   --> */}
            </fieldset>
          </form>
          {/* <!-- /   --> */}
        </div>
      </div>
    </div>
  );
}

export default Editmember;
