//    import
import http from "../utils/http-common";

//      
const getEtcAll = () => {
  return http.get(`/etc`);
};

  //  count() + MulEprice   
const updateCountMulEprice = (count,eno) => {
  return http.put(`/etc/update/count/mulEprice?count=${count}&eno=${eno}`);
};

//      export 
export default {
  getEtcAll,
  updateCountMulEprice
};
