import React, { useEffect, useState } from "react";
import initMain from "../../../assets/js/main";
import "../../../assets/css/roomstyle.css";
import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";

import { FreeMode, Navigation, Thumbs } from "swiper";

/* eslint-disable */
function RoomDetail() {
  useEffect(() => {
    initMain();
  }, []);
  const [thumbsSwiper, setThumbsSwiper] = useState(null);
  return (
    <div className="body">
      <div id="untree_co--overlayer"></div>
      <div className="loader">
        <div className="spinner-border text-primary" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
      {/*    */}

      <div className="room--site-wrap">
        {/* 23.07.01 -  [ Nav            ] */}
        {/* nav  */}

        <div>
          {/* 23.07.01 -  [   modal       ] */}

          {/* booking now! modal */}

          <div
            class="modal fade bd-example-modal-xl"
            id="myModal"
            tabindex="0"
            role="dialog"
            aria-labelledby="myExtraLargeModalLabel"
            aria-hidden="true"
            data-backdrop="false"
          >
            <div class="modal-dialog modal-xl">
              <div class="modal-content">
                <div class="modal-header">
                  <h4 class="modal-title"></h4>
                  <button type="button" class="close" data-dismiss="modal">
                    &times;
                  </button>
                </div>
                <div class="modal-body">
                  {/*   */}
                  <div className="date">
                    <form autocomplete="off">
                      <label className="modalDateTitle">Date</label>
                      <input type="text" name="bookingdate" readonly />
                    </form>
                  </div>

                  {/*   */}
                  <div className="room">
                    <label className="modalRoomTitle">
                      Room
                      <Link to={"/package"} className="modalRoom">
                        room details {">"}
                      </Link>
                    </label>
                    <select className="room-select">
                      <option selected disabled value="">
                        Select Room Type
                      </option>
                      <option value="">Standard</option>
                      <option value="">Deluxe</option>
                      <option value="">Family</option>
                      <option value="">Suite</option>
                    </select>
                  </div>

                  {/*    */}
                  <div className="people">
                    <label className="modalPeopleTitle">People</label>
                    <select className="people-select">
                      <option selected disabled value="">
                        People
                      </option>
                      <option value="">1</option>
                      <option value="">2</option>
                      <option value="">3</option>
                      <option value="">4</option>
                      <option value="">5</option>
                      <option value="">6</option>
                      <option value="">7</option>
                      <option value="">8</option>
                      <option value="">9</option>
                    </select>
                  </div>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-primary btn-default">
                    Reserve
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* booking now modal end */}

          <div class="room--site-wrap">
            <nav class="untree_co--site-nav js-sticky-nav">
              <div class="container d-flex align-items-center">
                <div class="logo-wrap">
                  <Link to={"/"} class="untree_co--site-logo">
                    Cloud Hotel
                  </Link>
                </div>
                <div class="site-nav-ul-wrap text-center d-none d-lg-block">
                  <ul class="site-nav-ul js-clone-nav">
                    <li class="active">
                      <Link to={"/"}>Home</Link>
                    </li>
                    <li class="has-children">
                      <Link to={"/room"}>Room</Link>
                    </li>
                    <li>
                      <Link to={"/dining"}>dining</Link>
                    </li>
                    <li>
                      <Link to={"/package"}>Package</Link>
                    </li>
                    <li>
                      <Link to={"/about"}>About Us</Link>
                    </li>
                    <li>
                      <Link to={"/contact"}>Contact</Link>
                    </li>
                    <li>
                      {/* <!-- Trigger the modal with a button --> */}
                      <Link
                        class="navBooking"
                        data-toggle="modal"
                        data-target="#myModal"
                      >
                        Booking Now !
                      </Link>
                    </li>
                  </ul>
                </div>

                <div class="icons-wrap text-md-right">
                  <ul class="icons-top d-none d-lg-block">
                    <li class="mr-4">
                      <a href="#" class="js-search-toggle">
                        <span class="icon-search2"></span>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <span class="icon-facebook"></span>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <span class="icon-twitter"></span>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <span class="icon-instagram"></span>
                      </a>
                    </li>
                  </ul>

                  {/* <!-- Mobile Toggle --> */}
                  <a
                    href="#"
                    class="d-block d-lg-none burger js-menu-toggle"
                    data-toggle="collapse"
                    data-target="#main-navbar"
                  >
                    <span></span>
                  </a>
                </div>
              </div>
            </nav>
            <nav class="untree_co--site-mobile-menu">
              <div class="close-wrap d-flex">
                <a href="#" class="d-flex ml-auto js-menu-toggle">
                  <span class="close-label">Close</span>
                  <div class="close-times">
                    <span class="bar1"></span>
                    <span class="bar2"></span>
                  </div>
                </a>
              </div>
              <div class="site-mobile-inner"></div>
            </nav>
          </div>
        </div>

        {/*   */}
        <main class="untree_co--site-main">
          <div class="untree_co--site-section">
            <div class="container-fluid px-0">
              <div class="row justify-content-center text-center pt-0 pb-5">
                <div class="col-lg-6 section-heading" data-aos="fade-up">
                  <h3 class="text-center">Standard</h3>
                </div>
              </div>
              {/* TODO */}
              <div class="row swiper-all">
                <div
                  class="col-9 relative"
                  data-aos="fade-up"
                  data-aos-delay=""
                  animateOut="fadeOut"
                >
                  <Swiper
                      style={{
                        "--swiper-navigation-color": "#fff",
                        "--swiper-pagination-color": "#fff",
                      }}
                      loop={true}
                      spaceBetween={4}
                      navigation={true}
                      thumbs={{ swiper: thumbsSwiper }}
                      modules={[FreeMode, Navigation, Thumbs]}
                      className="rdSwiper"
                    >
                      <SwiperSlide className="stand1"></SwiperSlide>
                      <SwiperSlide className="stand2"></SwiperSlide>
                      <SwiperSlide className="stand3"></SwiperSlide>
                      <SwiperSlide className="stand4"></SwiperSlide>
                    </Swiper>
                  <Swiper
                      onSwiper={setThumbsSwiper}
                      spaceBetween={4}
                      loop={true}
                      slidesPerView={1}
                      freeMode={true}
                      watchSlidesProgress={true}
                      modules={[FreeMode, Navigation, Thumbs]}
                      className="rdSwiper2"
                    >
                      <SwiperSlide className="stand2"></SwiperSlide>
                      <SwiperSlide className="stand3"></SwiperSlide>
                      <SwiperSlide className="stand4"></SwiperSlide>
                      <SwiperSlide className="stand1"></SwiperSlide>
                    </Swiper>
                </div>

                <a href="#">
                  <span
                    className="btn3 badge rounded-pill bg-info"
                    role="button"
                  >
                    
                  </span>
                </a>
              </div>
            </div>
          </div>
        </main>
        <div class="s011-information">
          <input type="hidden" name="roomonlycode" value="MNGKE" />
          <div class="room-information__title col"></div>
          <div class="room-information__item">
            <div class="item-information__title col"></div>
            <div class="item-information__list col">
              <ul>
                <li>/</li>
              </ul>
            </div>
          </div>
          <div class="room-information__item">
            <div class="item-information__title col"></div>
            <div class="item-information__list col">
              <ul>
                <li>1~2</li>
              </ul>
            </div>
          </div>
          <div class="room-information__item">
            <div class="item-information__title col"></div>
            <div class="item-information__list col">
              <ul>
                <li></li>
              </ul>
            </div>
          </div>
          <div class="room-information__item">
            <div class="item-information__title col"></div>
            <div class="item-information__list col">
              <ul>
                <li>34.4~37 </li>
              </ul>
            </div>
          </div>
          <div class="room-information__item">
            <div class="item-information__title col">/</div>
            <div class="item-information__list col">
              <ul>
                <li>15:00/11:00</li>
              </ul>
            </div>
          </div>
        </div>
        <div class="s124-3-7-parsys aem-GridColumn aem-GridColumn--default--12">
          <div class="s124  ">
            <div class="s124-information">
              <div class="s124-information_inner row align-items-center">
                {/* <div class="s124-information__title"> */}
                {/* <h2></h2> */}
                {/* <div> */}
                <div class="s124-information_title1 col"></div>

                <ul class="s124-information_title col-9">
                  <li class="s023-content__text"> </li>

                  <li class="s023-content__text">
                      (8F)' 
                  </li>

                  <li class="s023-content__text">
                     12 ( 6)    
                    
                  </li>

                  <li class="s023-content__text">
                     /   (7~8 :   1)
                  </li>

                  <li class="s023-content__text">
                       (36~10 )
                  </li>

                  <li class="s023-content__text">
                        or     (1 2)
                  </li>

                  <li class="s023-content__text"> </li>

                  <li class="s023-content__text"> </li>

                  <li class="s023-content__text"> </li>

                  <li class="s023-content__text">
                         (, )
                  </li>

                  <li class="s023-content__text">  </li>

                  <li class="s023-content__text">
                        :     
                    
                  </li>
                </ul>
                {/* </div> */}
                {/* </div> */}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="room-amenities__wrap row align-items-center">
        <div class="room-amenities__title col"></div>
        <div class="room-amenities__item">
          <div class="item__title col"></div>
          <div class="item__list col">
            <ul>
              <li>49 UHD TV</li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
        <div class="room-amenities__item">
          <div class="item__title1 col"></div>
          <div class="item__list col">
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li> </li>
              <li></li>
              <li> (Diptyque)</li>
              <li> </li>
              <li></li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
        </div>
        <div class="room-amenities__item">
          <div class="item__title2 col"></div>
          <div class="item__list col">
            <ul>
              <li>  1 2</li>
              <li>   </li>
              <li>  TV </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RoomDetail;
