//    import
import http from "../utils/http-common";

//  ID   
const getLikeAllById = (id) => {
  console.log(id);
  return http.get(`/like?id=${id}`);
};

//    (data(  ))
const createLike = data => {
  return http.post(`/like`, data);
};

// Like()  
const deleteLike = (lno) => {
  return http.delete(`/like/deletion/${lno}`);
};


//      export 
export default {
  getLikeAllById,
  createLike,
  deleteLike
};