/* eslint-disable */
export default function initmousehover() {
  // mouseenter    
  $(".single-portfolio a img").mouseenter(function () {
      let changeImage = $(this).attr("src");
      $("#amenities_page").css({
        "background-image": "url(" + changeImage + ")",
      });
    });


  // mouseenter     
    $(".single-portfolio a img").mouseleave(function () {
      let changeImage = $(this).attr("src");
      $("#amenities_page").css({
        "background-image": "url(" + changeImage + ")",
        
      });
    });
}
