import React from "react";
import { useState, useEffect } from "react";


// Pagination   import
import Pagination from "@material-ui/lab/Pagination";

//   js  import
import NoticeDataService from "../../services/NoticeDataService";

function Notice() {
  //  
  //     
  const [notice, setNotice] = useState([]);

  //    
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [pageSize, setPageSize] = useState(3);
  const pageSizes = [3, 6, 9];

  //  

  //    
  // [page, pageSize] : page, pageSize   useEffect 
  useEffect(() => {
    retrieveNotice(); //    
  }, [page, pageSize]);




  //   
  const retrieveNotice = () => {
    //    
    NoticeDataService.getAll(page - 1, pageSize)
      .then((response) => {
        //  : response.data(   )
        const { notice, totalPages } = response.data;

        //    
        setNotice(notice);
        //     
        setCount(totalPages);

        console.log(response.data);
      })
      .catch((e) => {
        // 
        console.log(e);
      });
  };

  //  :   
  const handlePageChange = (event, value) => {
    setPage(value);
  };

  //  :   
  const handlePageSizeChange = (event) => {
    setPageSize(event.target.value);
    setPage(1);
  };

  return (
    <div>
      <div className="col-lg-6 mx-auto notice_top">
        <div className="notice_inner">
          <h1 className="notice_title">NEWS</h1>
          <p className="notice_content">
               
          </p>
        </div>
      </div>

      <div className="notice_inner"> 
        <div className="row">
          {/* <div className="col-lg-10 mx-auto "> */}
          <div className="col-lg-10 mx-auto ">
            {/*  col-lg-6      */}
            {/* page   */}
            <div className="col-md-8 offset-7">
              <div className="mb-5">
                Items per Page:
                <select value={pageSize} onChange={handlePageSizeChange}>
                  {pageSizes.map((size) => (
                    <option key={size} value={size}>
                      {size}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            {/* page   */}

            <table className="table tcontent">
              <thead>
                <tr>
                  <th scope="col" ></th>
                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {notice &&
                  notice.map((data, index) => (
                    <tr>
                      <td> <a href={data.titleUrl}>{data.title}</a></td>
                      <td>{data.insertTime}</td>
                    </tr>
                  ))}
              </tbody>
            </table>

            {/* page   */}
            <div className="col-md-8 offset-3">
              <Pagination
                className="my-3"
                count={count}
                page={page}
                siblingCount={1}
                boundaryCount={1}
                variant="outlined"
                shape="rounded"
                onChange={handlePageChange}
              />
            </div>
            {/* page   */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Notice;
