//  :  
//   :    
import React, { useState, useRef } from "react";
import "../../assets/css/login.css";
// (Redux) import:
//  : useDispatch,  : useSelector
import { useDispatch, useSelector } from "react-redux";
//  import
import { register } from "../../store/actions/auth";

//    import
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
//  url  :   import
import { Navigate, useNavigate } from "react-router-dom";
//   
// required   
const required = (value) => {
  // value   :  
  if (!value) {
    return (
      //    
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

//    
const vusername = (value) => {
  // 3 < value() <= 20 : 
  //      
  if (value.length < 3 || value.length > 20) {
    return (
      //  
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 20 characters.
      </div>
    );
  }
};

const vId = (value) => {
  if (value.length < 3 || value.length > 20) {
    return (
      //  
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 20 characters.
      </div>
    );
  }
};

//    
const vpassword = (value) => {
  // value : 
  // 6 < value <= 40 : 
  //     
  if (value.length < 6 || value.length > 40) {
    //   
    return (
      <div className="alert alert-danger" role="alert">
        6  40   
      </div>
    );
  }
};

//    
const validEmail = (value) => {
  // value  isEmail   
  // false   email  
  if (!isEmail(value)) {
    return (
      //    
      <div className="alert alert-danger" role="alert">
          .
      </div>
    );
  }
};

//    
function vphNumber(phoneNumber) {
  const regex = /^010(\d{3}|\d{4})\d{4}$/;

  if (regex.test(phoneNumber)) {
    console.log("   .");
  } else {
    console.log("   .");
  }
}

//  
function Register() {
  // TODO :  
  const form = useRef();
  const checkBtn = useRef();
  let navigate = useNavigate();

  // Id  
  const [id, setId] = useState("");
  //   
  const [username, setUsername] = useState("");
  //   
  const [password, setPassword] = useState("");
  //   
  const [email, setEmail] = useState("");
  //   
  const [phone, setPhone] = useState("");
  //     successful = true
  //  
  const [successful, setSuccessful] = useState(false);
  //      
  const { message } = useSelector((state) => state.message);
  //    
  const dispatch = useDispatch();

  // TODO : 
  //  :     
  // 
  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  // 
  const onChangeId = (e) => {
    const id = e.target.value;
    setId(id);
  };

  //  :     
  // 
  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };

  //  :     
  // 
  const onChangeEmail = (e) => {
    const email = e.target.value;
    setEmail(email);
  };

  //  :     
  // 
  const onChangePhone = (e) => {
    const phone = e.target.value;
    setPhone(phone);
  };

  //  
  const handleRegister = (e) => {
    e.preventDefault(); // form   

    setSuccessful(false); // (false)

    //    
    form.current.validateAll();

    //    0  
    if (checkBtn.current.context._errors.length === 0) {
      //   : reqister (_)
      dispatch(register(username, id, password, email, phone))
        .then(() => {
          // 
          setSuccessful(true); //   
          navigate("/"); //  /profile  

        })
        .catch(() => {
          // 
          setSuccessful(false); //    
        });
    }
  };

  return (
    <div className="body">
      <div className="signup-wrapper">
        <h2></h2>
        <div className="signup-form">  
          
          {/* Form  :  ,  ref= {form} */}
          <Form onSubmit={handleRegister} ref={form}>
            {/* successful  false   div  */}
            {/* successful  false    */}
            {!successful && (
              <div>
                <div className="form-group">
                  {/* Input :    */}
                  {/* validations  : required, vusername   */}
                  {/* validations={[1, 2...]} */}
                  {/* required  :   () */}
                  {/* vusername  :  3  20  */}
                  <Input
                    type="text"
                    className="form-control"
                    name="username"
                    placeholder="Name"
                    value={username}
                    onChange={onChangeUsername}
                    validations={[required, vusername]}
                  />
                </div>

                <div className="form-group">
                  {/* Input :   (email) */}
                  <Input
                    type="text"
                    className="form-control"
                    name="id"
                    placeholder="Id"
                    value={id}
                    onChange={onChangeId}
                    validations={[required, vId]}
                  />
                </div>

                <div className="form-group">
                  {/* Input :   (password) */}
                  <Input
                    type="password"
                    className="form-control"
                    name="password"
                    placeholder="Password"
                    value={password}
                    onChange={onChangePassword}
                    validations={[required, vpassword]}
                  />
                </div>

                <div className="form-group">
                  {/* Input :   (email) */}
                  <Input
                    type="text"
                    className="form-control"
                    name="email"
                    placeholder="Email"
                    value={email}
                    onChange={onChangeEmail}
                    validations={[required, validEmail]}
                  />
                </div>

                <div className="form-group">
                  {/* Input :   (password) */}
                  <Input
                    type="phone"
                    className="form-control"
                    name="phone"
                    placeholder="Phone Number"
                    value={phone}
                    onChange={onChangePhone}
                    validations={[required, vphNumber]}
                  />
                </div>

                <div className="form-button1">
                  <button className="submit2">
                    
                  </button>
                </div>
              </div>
            )}

            {/* message :       */}
            {message && (
              <div className="form-group">
                <div
                  className={
                    successful ? "alert alert-success" : "alert alert-danger"
                  }
                  role="alert"
                >
                  {message}
                </div>
              </div>
            )}
            {/* CheckButton :  ,  ref={checkBtn} */}
            {/*     CheckButton   */}
            <CheckButton style={{ display: "none" }} ref={checkBtn} />
          </Form>
        </div>
      </div>
    </div>
  );
}

export default Register;
