// types.js (  ) import
import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  SET_MESSAGE,
} from "./types";

//    import
import AuthService from "../../services/auth.service";

//   
export const register = (username, id, password, email, phoneNumber) => (dispatch) => {
  //    
  return AuthService.register(username, id, password, email, phoneNumber)
    .then((response) => {
      // 
      // 1st (auth)
      dispatch({
        type: REGISTER_SUCCESS,  //  
      });

      // 2nd (message)
      dispatch({
        type: SET_MESSAGE,  //   
        payload: response.data.message,  //   
      });

      //   :  (resolve())
      return Promise.resolve();
    })
    .catch((error) => {
      // 
      // ()
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

        // 1st (auth)
      dispatch({
        type: REGISTER_FAIL,  //   
      });

      // 2nd (message)
      dispatch({
        type: SET_MESSAGE,  //   
        payload: message,  //  
      });

      return Promise.reject();
    });
};

//  
export const login = (id, password) => (dispatch) => {
  // 1)  (login() )  
  return AuthService.login(id, password)
    .then(
      // 
      // data :  
      (data) => {
        // () 
        // payload :  (())
        dispatch({
          type: LOGIN_SUCCESS,  //  
          payload: { user: data },  // 
        });

        // Promise   : resolve() :  
        return Promise.resolve();
      }
    )
    .catch((error) => {
      // ()
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

        // 1st (auth)
      dispatch({
        type: LOGIN_FAIL,  //   
      });
      // 2nd (message)
      dispatch({
        type: SET_MESSAGE,  //   
        payload: message,  //  
      });

        // Promise   : reject() :  
      return Promise.reject();  
    });
};

//  
export const logout = () => (dispatch) => {
  // 1)  user  
  AuthService.logout();

  // 2)   (  : LOGOUT )
  dispatch({
    type: LOGOUT,
  });
};
